import React, { Component } from 'react'

class Home extends Component {
  render() {
    return (
      <div>
        <br />
        <div className="jumbotron border-primary">
          <h1 className="display-3">LCLO group</h1>
          <p className="lead">
            Welcome to the content portal for the LCLO group site. Select one of
            the pages above to add a new post{' '}
          </p>
          <hr className="my-4" />
          <p></p>
          <p className="lead">
            <a className="btn btn-primary btn-lg" href="#" role="button">
              Visit LCLO site
            </a>
          </p>
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
        </div>
      </div>
    )
  }
}

export default Home



